import { Component } from '@angular/core';
import { IonicPage,Tabs, NavController, NavParams, ToastController, AlertController,LoadingController, Platform } from 'ionic-angular';
import { ApiServiceProvider } from '../../providers/api-service/api-service';
import { Storage } from '@ionic/storage';
import { Events } from 'ionic-angular';

@IonicPage()
@Component({
  selector: 'page-employeealert',
  templateUrl: 'employeealert.html',
})
export class EmployeealertPage {
  alertcount: any;
  postdata: any;
  postid: any;
  msg: any;
  runhome: number;
  SessionId: any;
  title: any;
  arr:any;
  notification_id: any;
  allData: any;
  apiData: any;
  deviceId: any;
  loading:any;
  login_token: any;
  employeeId: any;
  value: any='0';
  myInfiniteScroll:any;
  infiniteScrollCalled:boolean=false;
  hideInfiniteScroll:boolean=false;
  alert121:any;
  public unregisterBackButtonAction: any;
  closeAppPopupShow:number=0;
  bgImageViewer:boolean=false;
  secondarr:any;
  // tab:Tabs;
  tab:Tabs;
  constructor(public navCtrl: NavController, public navParams: NavParams,public events: Events,
    private alertCtrl: AlertController, private platform: Platform, public toastCtrl: ToastController,
     private storage: Storage, private apiServices: ApiServiceProvider,public loadingCtrl: LoadingController) {
       this.runhome=1;
       this.tab = this.navCtrl.parent;
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad EmployeealertPage');
  }

 //Common Loader:
 commonLoader() {
  this.loading = this.loadingCtrl.create({
    enableBackdropDismiss: true,
    spinner: 'ios-small',
    content: 'Loading data...'
  });
  this.loading.present();
}


  alertlist() {
    this.commonLoader();
    this.storage.get('employeeId').then(data => {
      this.employeeId = data;
      console.log(' employeeId== ', this.employeeId);
    });
    this.storage.get('login_token').then((data) => {
      console.log('showOnBoard value is111==', data);
      this.login_token = data;
    })
    this.storage.get('deviceId').then(data => {
      this.deviceId = data;
      //  this.token='Q3owSXo3T05BVVJscHREVCsyeUtqZz09OjoWf4jZSksqJnff2wxdlZ7e'
      let apiKey = {

        "client_id": this.apiServices.clientId,
        "employee_id": this.employeeId,
        "device": this.apiServices.device,
        "device_id": this.deviceId,
        "app_version": this.apiServices.appVersion,
        "value":this.value
      };
      this.apiServices.EmployeeAlert(apiKey, this.login_token)
        .subscribe((res) => {
          this.loading.dismiss();
          // this.apiServices.urllist(apiKey,this.token).then((result) => { 
          console.log('', res);
          this.apiData=res;
this.alertcount=this.apiData.alert_count;
// alert(this.alertcount);


          // this.over_all_progress=this.apiData.this.apiData;
          if (res.success == 1) {
           
            if(this.allData==undefined){
              this.allData = res.data;
            }else{
              this.allData= this.allData.concat(res.data);
              console.log('new album list==',res.data );
            }
                if(this.infiniteScrollCalled==true){
                  this.myInfiniteScroll.complete();
                  this.infiniteScrollCalled=false;
                }
            console.log('this.formdata==',this.allData);
          
            this.hideInfiniteScroll=false;



            if(this.alertcount==0 || this.alertcount=='0')
            {
              // alert(this.alertcount);
              this.alertcount=null;
              this.events.publish('emptabbadgedata', this.alertcount);
            }

          } else {
            if(this.value=='0'){
              this.msg=res.message;
              this.bgImageViewer = true;
            }
            console.log('this.formdata==',this.allData);
            this.hideInfiniteScroll=true;
            if(this.allData.length!=0){
              this.apiMessage(res.message);
            }

          }

        }, (err) => {
          console.log('err== ', err);
          this.apiMessage(err);

        });

    });
  }
  apiMessage(message) {
    const toast = this.toastCtrl.create({
      message: message,
      duration: 3000
    });
    toast.present();
  }



  ionViewWillEnter(){
    this.value=0;
    this.allData=[];
    this.alertlist();
    this.initializeBackButtonCustomHandler();
  }


  doInfinite(infiniteScroll) {
    console.log('Begin async operation');
  this.myInfiniteScroll=infiniteScroll;
    setTimeout(() => {
      this.infiniteScrollCalled=true;
      this.value=this.apiData.lastId;
      this.alertlist();
      console.log('Async operation has ended');
      // this.myInfiniteScroll.complete();
    }, 500);
  }
  
  gotonext(axdata,data){
    // this.alertstatus();
    console.log('axdata',axdata);
    this.notification_id=data.refrence_id;
    console.log('this.notification_id',this.notification_id);
    if(axdata=='survey'){
      this.navCtrl.push('TestsurveyPage',{'surveyid':this.notification_id});
    }
    
    if(axdata=='news'){
      this.postid={"auto_id":this.notification_id}
      this.navCtrl.push('EmpnewsdetailPage',{'data':this.postid});
    }
    
    if(axdata=='welcome_aboard_comment' || axdata=='welcome_aboard_like' || axdata=='welcome_aboard_create'){
      this.postid={"welcomeAboard_AutoId":this.notification_id}
      this.navCtrl.push('WelcomeOnboardDetailsPage',{'data':this.postid});
    }
    if(axdata=='nomination_detail_admin' || axdata=='nomination_detail'){
      if(axdata=='nomination_detail_admin'){
        this.postid={"nomination_id":this.notification_id,"user_authority":'a'}
      }else{
        this.postid={"nomination_id":this.notification_id,"user_authority":'n'}
      }
  
      this.navCtrl.push('NominationApprovalPage',{'data':this.postid});
    }


    if(axdata=='nomination_detail_page' || axdata=='nomination_detail_page'){
      this.postid={"nomination_id":this.notification_id,"flag":data.refrence_flag}
      this.navCtrl.push('HomeawardsdetailPage',{'nominationData':this.postid});
    }


    if(axdata=='certificate_page'){
      // this.postid={"nomination_id":this.notification_id,"flag":data.refrence_flag}
      this.navCtrl.push('MycertificatesPage');
    }


    if(axdata=='Contest' || axdata=='Contest'){
      this.postid={"auto_id":this.notification_id,"flag":data.refrence_flag}
      this.navCtrl.push('ContestdetailPage',{'data':this.postid});
    }


    if(axdata=='notice_board'){
      this.postid={"auto_id":this.notification_id}
      this.navCtrl.push('NoticeDetailsPage',{'data':this.postid});
    }

    if(axdata=='leadership'){
      this.postid={"auto_id":this.notification_id}
      this.navCtrl.push('LeadershiptalkdetailPage',{'data':this.postid});
    }

    if(axdata=='quick_byte'){
      this.postid={"auto_id":this.notification_id}
      this.navCtrl.push('QuickBytesDetailsPage',{'data':this.postid});
    }

    if(axdata=='album_detail'){
      this.postid={"auto_id":this.notification_id}
      // this.albumid={"albumid":}
      this.navCtrl.push('AlbumImageSliderPage',{'data':this.postid,'first_post_id':'1','albumid':data.main_id});
    }

    if(axdata=='community_post' || axdata=='community_like' || axdata=='community_comment'){
      this.postid={"community_post_id":this.notification_id}
      this.navCtrl.push('CommunityDetailsPage',{'data':this.postid});
    }

    if(axdata=='work_aniversery' || axdata=='work_aniversery_comment'){
      this.navCtrl.push('WorkanniversaryPage',{'data':data});
    }

    if(axdata=='wow_award_received' || axdata=='wow_award_liked' || axdata=='wow_award_commented' || axdata=='wow_award'){
      // this.postid={"community_post_id":this.notification_id}
      this.navCtrl.push('BadgeBoardPage');
    }

    if(axdata=='nomination_creation' || axdata=='nomination_approval'){
      // this.postid={"nominationid":this.notification_id}
      this.navCtrl.push('AwardeddetailPage',{'nominationid':this.notification_id});
    }
    if(axdata=='photowall'){
      // this.postid={"nominationid":this.notification_id}
      this.navCtrl.push('PhotowallAllImagesPage',{'postId':this.notification_id});
    }
    if(axdata=='photowall_image'){
      // this.postid={"nominationid":this.notification_id}
      this.navCtrl.push('PhotowallImageSliderPage',{'postId':this.notification_id,'selectedIndex':1,'alertkey':1,'image_index':data.image_index});
    }
    if(axdata=='community'){
      this.postdata={"communityId":this.notification_id,"community_name":data.community_name,
    "join_status":data.join_status,"images":data.images,"total_members":data.total_members,
  "title":data.title,"description":data.description};
      console.log("postdata",this.postdata);
      this.navCtrl.push('CommunityPostsPage',{'data':this.postdata});
    }


      }



      // ionViewWillEnter(){
 
      //   this.initializeBackButtonCustomHandler();
      // }
      
      
      showeAlert() {
        this.alert121 = this.alertCtrl.create({
          title: 'App termination',
          message: 'Do you want to close the app?',
          buttons: [{
            text: 'Cancel',
            role: 'cancel',
            handler: () => {
              console.log('Application exit prevented!');
            }
          }, {
            text: 'Close App',
            handler: () => {
              this.platform.exitApp(); // Close this application
            }
          }]
        });
        this.alert121.present();
      }
      
      ionViewWillLeave() {
        this.unregisterBackButtonAction && this.unregisterBackButtonAction();
      }
      
      
      //Hardware Back Button
      
      initializeBackButtonCustomHandler(): void {
        let that = this;
        this.unregisterBackButtonAction = this.platform.registerBackButtonAction(function (event) {
          // that.closeAppPopupShow++;
          // if(that.closeAppPopupShow%2!=0){
          //   that.showeAlert();
          // }else{
          //   that.alert121.dismiss();
          // }
          // that.tab.select(1);
          // this.navCtrl.parent.select(1);
          that.navCtrl.parent.select(0);
        }, 101); // Priority 101 will override back button handling (we set in app.component.ts) as it is bigger then priority 100 configured in app.component.ts file */
      }
    


}



